<template>
  <div class="maina clearfix">
    <card-item @click="alert(item)" :key="index" v-on:newNodeEvent="cardShow" v-for="(item ,index) in numberList" v-bind:number="item"  v-bind:indexc="index"></card-item> 
    <transition name="fade" mode="out-in" appear>
      <div class="gameBox" v-if="isWin==0">
        <h2>You Win</h2>
        <div>总用时:{{$root.$children[0].$children[0].timer}}</div>
        <div>总步数:{{$root.$children[0].$children[0].step}}</div>
        <div><a @click="reset">再玩一次</a></div>
      </div>
    </transition>
  </div>
</template>

<script>
  import CardItem from './CardItem'
  import store from '@/vuex/store'
export default {
  name: 'Maina',
  data () {
    return {
      numberList: [1,2,3,4,5,6,6,7],
      numItem:16,
      oneObject:'',
      secondOject:'',
      isOne : 1
    }
  },
  methods:{
    cardShow:function(that){
      if(!this.isOne){
        
        console.log('判断是否')
        if(this.curState==1){
          if(this.$store.state.oneNumber==this.$store.state.secondNumber){
           
           }else{
              console.log('dont match')
              this.oneObject.cardState2=1
              this.oneObject.cardState=0
              this.secondOject.cardState2=1
              this.secondOject.cardState=0
           }
           
      
        } 
      }
      console.log(that)
      that.msg = 'aaa'
      that.cardState2 = 0
      that.cardState = 1
     if(this.curState==1){
        this.oneObject = that
      }else if(this.curState==0){
        this.secondOject = that
      }

      this.isOne = 0
      this.$root.Bus.$emit('eventName',123)
    },
    reset(){
      this.$store.dispatch('reset')
      this.$root.Bus.$emit('eventName',0)
      var newList=[]
      for(var i=0;i<this.numItem/2;i++ ){
      
        newList.push(i+1)
        newList.push(i+1)
      }
      this.numberList =newList
      this.numberList.sort(function(){ return 0.5 - Math.random() })
      console.log(this.$root.$children[0].$children[0].timer)
      this.$children.forEach((index)=>{
        index.cardState=0;
        index.cardState2=1;
      })
    }
  },
  mounted(){
    var newList=[]
    for(var i=0;i<this.numItem/2;i++ ){
    
      newList.push(i+1)
      newList.push(i+1)
    }
    this.numberList =newList
    this.numberList.sort(function(){ return 0.5 - Math.random() })
    console.log()

  },
  components:{
    CardItem
  },
  computed:{
    curState(){
      return this.$store.state.curState
    },
    isWin(){
      return this.$store.state.totalPage
    }
  },
  watch:{
    curStatea(val){
      this.curState = val
    },
    isWin(val){
      if(val==0){
       this.$root.Bus.$emit('stopu',123)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden}
.clearfix{*+height:1%;}
.maina{
      margin: auto;
    display: flex;
    flex-flow: initial;
    flex-shrink: initial;
    flex-wrap: wrap-reverse;
    flex-basis: initial;
    justify-content: center;
}
.gameBox{position:absolute;top:250px;width:250px;height:250px;background:#FAF8EE;border:1px solid #010101;border-radius:5px;}
.gameBox h2{text-align:center}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
.gameBox>div{text-align:center;padding:5px}
.gameBox a{    display: inline-block;
    width: 62px;
    height: 25px;
    background: #597AAE;
    color: #fff;
    line-height: 25px;
    border-radius: 4px;
    margin-top: 69px;cursor:pointer }
</style>



