<template>
  <div class="cardItem box viewport-flip" @click="alertc(number,indexc,$event)" >
    indexc
    <template v-if="number==1">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/1.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==2">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/2.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==3">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/3.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==4">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/4.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==5">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/5.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==6">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/6.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==7">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/7.png" width="100%" height="100%" /></a>
    </template>
    <template v-if="number==8">
      <a class="list flip " :class="{'in':cardState,'out':cardState2}"><img src="../assets/8.png" width="100%" height="100%" /></a>
    </template>
    <a class="list flip" :class="{'out':cardState,'in':cardState2}"><img src="../assets/back.png" width="100%" height="100%" /></a>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
import store from '@/vuex/store'
export default {
  name: 'cardItem',
  props:['number','indexc'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      cardState:0,
      cardState2:1
    }
  },
  methods:{

    alertc(numberc,indexc,e){
      e.preventDefault()
      if(this.$store.state.oneIndex==indexc) return false
      var indexd = indexc
      this.$emit('newNodeEvent', this)
      let array = [numberc,indexc]
      this.$store.dispatch('clickItem',array);
      
    }
  }
}
document.ondragstart = function() {
    return false;
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.cardItem{
  width:100px;
  height:105px;
  float:left;
  text-align:center;
  background-size:100% 100%;
  margin:5px
}
.cardItem>.list{position:absolute;left:0;top:0;display:block;width:100%;height:100%}
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.viewport-flip {
    -webkit-perspective: 1000;
    perspective: 1000;
    position: relative;
}
.flip {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateX(0); /* Needed to work around an iOS 3.1 bug that causes listview thumbs to disappear when -webkit-visibility:hidden is used. */
    backface-visibility: hidden;/*backface-visibility 属性定义当元素不面向屏幕时是否可见*/
    transform: translateX(0);
}
.flip.out {
    -webkit-transform: rotateY(-90deg) scale(.9);
    -webkit-animation-name: flipouttoleft;
    -webkit-animation-duration: 175ms;
    transform: rotateY(-90deg) scale(.9);
    animation-name: flipouttoleft;
    animation-duration: 175ms;
}
.flip.in {
    -webkit-animation-name: flipintoright;
    -webkit-animation-duration: 225ms;
    animation-name: flipintoright;
    animation-duration: 225ms;
}
.flip.out.reverse {
    -webkit-transform: rotateY(90deg) scale(.9);
    -webkit-animation-name: flipouttoright;
    transform: rotateY(90deg) scale(.9);
    animation-name: flipouttoright;
}
.flip.in.reverse {
    -webkit-animation-name: flipintoleft;
    animation-name: flipintoleft;
}
@-webkit-keyframes flipouttoleft {
    from { -webkit-transform: rotateY(0); }
    to { -webkit-transform: rotateY(-90deg) scale(.9); }
}
@keyframes flipouttoleft {
    from { transform: rotateY(0); }
    to { transform: rotateY(-90deg) scale(.9); }
}
@-webkit-keyframes flipouttoright {
    from { -webkit-transform: rotateY(0) ; }
    to { -webkit-transform: rotateY(90deg) scale(.9); }
}
@keyframes flipouttoright {
    from { transform: rotateY(0); }
    to { transform: rotateY(90deg) scale(.9); }
}
@-webkit-keyframes flipintoleft {
    from { -webkit-transform: rotateY(-90deg) scale(.9); }
    to { -webkit-transform: rotateY(0); }
}
@keyframes flipintoleft {
    from { transform: rotateY(-90deg) scale(.9); }
    to { transform: rotateY(0); }
}
@-webkit-keyframes flipintoright {
    from { -webkit-transform: rotateY(90deg) scale(.9); }
    to { -webkit-transform: rotateY(0); }
}
@keyframes flipintoright {
    from { transform: rotateY(90deg) scale(.9); }
    to { transform: rotateY(0); }
}
</style>



