<template>
  <div class="header">
    <div class="clearfix">
      <div class="fl h_1">Memory</div>
      <div class="fl h_2"><p @click="startTimer">时间：{{timer}}</p></div>
      <div class="fl h_3"><p>使用步数：{{step}}</p><p>9999</p></div>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/store'
export default {
  name: 'Headera',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      timer: 0,       //默认倒数30秒
      stop : false,   //默认是停止的，但界面加载之后会变成false
      Interval:""  //setInterval的对象
    }
  },
  computed:{
    step(){
      return this.$store.state.step
    }
  },
  mounted(){
  console.log(this.$root)
     this.$root.Bus.$on('eventName', value => {
        if(value==0){
          this.update()
          this.timer=0;
          this.step=0
          this.stop = false
        }else{
          this.startTimer()
        }

     })
     this.$root.Bus.$on('stopu', value => {
       this.update()
     })
  },
  methods:{
    update(){
       clearInterval(this.Interval)
    },
    startTimer:function(){
      console.log(this)
      var that = this
   //   alert(1)
      if(this.stop == false){
        console.log(123)
        this.Interval = setInterval(()=>{
          console.log("in update fn")
            this.timer++
        },1000) 
       
      }else{
      // 
      }
      this.stop = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden}
.clearfix{*+height:1%;}
.fl{float:left}
.h_1,.h_2,.h_3{height:100px;border-radius:10px;color:#fff;text-align:center;font-size:19px;line-height:100px;margin:8.8px }
.h_1{width:157px;background:#597AAE}
.h_2,.h_3{width:124px;background:#BBAD9F}
</style>



