import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = {
	curState:1,
	oneNumber:'',
	secondNumber:'',
	oneIndex:'',
	secondIndex:'',
	step:0,
	totalPage:16
}
const mutations = {
	CURCLICK_REDUCT(state){
		state.curState--
		if(state.curState==-1){
			state.curState=1
		}
	},
	RESET_CURSTATE(state){
		state.curState=1
	},
	SET_FIRST(state,oneNumber,index){
		state.oneNumber	= oneNumber
		
	},
	SET_SECOND(state,secondNumber,index){
		state.secondNumber	= secondNumber
		
	},
	SET_FIRSTINDEX(state,index){
		state.oneIndex	= index
	},
	SET_SECONDINDEX(state,index){
		state.secondIndex	= index
	},
	ADDSTEP(state){
	
		state.step++
	},
	SET_ZERO(state){
		state.curState=1,
		state.oneNumber='',
		state.secondNumber='',
		state.oneIndex='',
		state.secondIndex='',
		state.step=0,
		state.totalPage=16
	}
}
const actions = {
	clickItem({commit},oneNumber){
		console.log(this.state.curState)
	
		commit('ADDSTEP')
		if(this.state.curState==1){
			commit('SET_FIRST',oneNumber[0])
			commit('SET_FIRSTINDEX',oneNumber[1])
		}else if(this.state.curState==0){
			commit('SET_SECOND',oneNumber[0])
			commit('SET_SECONDINDEX',oneNumber[1])
			if(this.state.oneNumber==this.state.secondNumber){
	          this.state.totalPage=this.state.totalPage-2
	          if(this.state.totalPage==0){
	           
	          }
	        }

		}
		commit('CURCLICK_REDUCT')
	},
	reset({commit}){
		commit('SET_ZERO')
	}
}
const getters = {

}
export default new Vuex.Store({
	state,
	mutations,
	actions
})


